<template>
    <div id="box" style="position: absolute;width:100%;left:0;top:0">
        <div class="desc">
            <h3>{{name}}</h3>
        </div>

        <button  class="button">
            比对成功
        </button>
    </div>
</template>
<script>
    export default {
        name: 'start',
        data() {
            return {
                name:'张三'
            }
        },
        methods: {
        }
    }
</script>

<style scoped>

    #box {
        width: 100%;
        height: 100vh;
        background-image: url('../assets/image/successa.png');
        background-repeat: no-repeat;
        background-size: 100% 100%;
        border-top: 1px solid transparent;
    }
    .desc{
        text-align: center;
        margin-top: 2rem;
    }
    .desc h3{
        font-size: 0.38rem;
        color: #ffffff;
        line-height: 1.5;
    }
    .button {
        display: block;
        width: 4.4rem;
        height: 1.8rem;
        background:rgba(0,0,0,0) url("../assets/image/button4.png") no-repeat;
        background-size: 100% 100%;
        margin: 0 auto;
        margin-left: -2.2rem;
        position: absolute;
        bottom: 1rem;
        left: 50%;
        color: #fff;
        font-size: 0.3rem;
        text-align: center;
        border: none;
    }
</style>



