<template>
    <div class="recognition" @click="go" style="position: absolute;width:100%;left:0;top:0">
        <div class="recBox"></div>
        <div class="dotBox">
            <div class="dot dot1"></div>
            <div class="dot dot2"></div>
            <div class="dot dot3"></div>
            <div class="dot dot4"></div>
            <div class="dot dot5"></div>
        </div>
        <div class="text">识别中&nbsp;&nbsp;别着急...</div>
        <div class="copyright">UNIQUE TECNOLOGY</div>

        <div class="over" v-show="flag">
            <div class="alert">
                <img src="../assets/image/rec.png" alt="">
                <div class="text">拍摄完成</div>
            </div>
        </div>
    </div>

</template>
<script>
export default {
  name: 'start',
  data () {
    return {
      flag:false
    }
  },
  methods:{
      go(){
          this.flag = true;
          setTimeout(()=>{
              this.$router.push('success')
          },3000)
      }
  }
}
</script>
<style scoped>
    .recognition{
        width: 100%;
        height: 100vh;
        background: #fbfbfb;
        color:#a0a0a0;
    }
    .recognition .recBox{
        width: 3.83rem;
        height: 5rem;
        background: #f6f6f6;
        border: 0.06rem solid #efefef;
        margin:2rem auto 2.58rem ;
    }
    .recognition .text{
        text-align: center;
        font-size: 0.24rem;
        margin-top: 0.53rem;
        margin-bottom: 1.33rem;
    }
    .recognition .copyright{
        width: 100%;
        text-align: center;
        font-size: 0.14rem;
        position: absolute;
        left: 0;
        bottom: 0.53rem;
    }

    .recognition .dotBox{
        width: 1.1rem;
        height: 0.16rem;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
    }
    .recognition .dotBox .dot{
        width: 0.16rem;
        height: 0.16rem;
        border-radius: 50%;
        background: #d1f0e4;
    }
    .recognition .dotBox .dot1{
        animation: change 1.5s 0.2s infinite alternate;
    }
    .recognition .dotBox .dot2{
        animation: change 1.5s 0.4s infinite alternate;
    }
    .recognition .dotBox .dot3{
        animation: change 1.5s 0.6s infinite alternate;
    }
    .recognition .dotBox .dot4{
        animation: change 1.5s 0.8s infinite alternate;
    }
    .recognition .dotBox .dot5{
        animation: change 1.5s 1s infinite alternate;
    }
    @keyframes change{
        0%{
            background: #d1f0e4;
        }
        50%{
            background: #7cdbb6;
        }
        100%{
            background: #27c588;
        }
    }

    .over{
        width: 4.8rem;
        height: 3.09rem;
        /*background: rgba(0, 0, 0, 0.1);*/
        position: fixed;
        left: 50%;
        top: 50%;
        margin-top: -1.54rem;
        margin-left: -2.4rem;
        z-index: 9999;
    }
    .over .alert{
        width: 4.8rem;
        height: 3.09rem;
        border-radius: 0.5rem;
        background: #fff;
        box-shadow:  0.14rem 0.14rem 0.68rem rgba(0, 0, 0, 0.1);
        padding-top: 0.45rem;
    }
    .over .alert img{
        display: block;
        margin: 0.47rem auto;
        width: 1.46rem;
        height: 1.06rem;
    }
    .over .alert .text{
        width: 100%;
        text-align: center;
        font-size: 0.24rem;
        color: #a3a3a3;
    }
</style>



