<template>
    <div id="app">
        <transition>
            <router-view/>
        </transition>

    </div>
</template>

<script>
    export default {
        name: 'App'
    }
</script>

<style  lang="scss">
    @mixin useRem($size){
        $device-list : 320px, 360px, 375px , 412px, 414px, 750px;
        html{
            @each $device in $device-list{
                @media screen and (min-width: $device){
                    font-size: 100px * ($device/$size);
                }
            }
        }
    }
    @include useRem(750px);
    .v-enter {
        opacity: 0;
    }

    .v-enter-to {
        opacity: 1;
    }

    .v-enter-active {
        transition: opacity 1s ease;
    }

    .v-leave {
        opacity: 1;
    }

    .v-leave-to {
        opacity: 0;
    }

    .v-leave-active {
        transition: opacity 1s ease;
    }

    html, body {
        background-color: #fff;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    * {
        margin: 0;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }

    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        width: 100%;
        height: 100%;
    }
</style>



