<template>
    <div class="recognition" @click="go" style="position: absolute;width:100%;left:0;top:0">
        <div class="recBox"></div>
        <div class="text">正脸对准屏幕</div>
        <div class="copyright">UNIQUE TECNOLOGY</div>

    </div>
</template>
<script>
export default {
  name: 'start',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
    methods:{
      go:function(){
          this.$router.push('successa');
      }
    }
}
</script>

<style scoped>
    .recognition{
        width: 100%;
        height: 100vh;
        background: #fbfbfb;
        color:#a0a0a0;
    }
    .recognition .recBox{
        width: 3.83rem;
        height: 5rem;
        background: #f6f6f6;
        border: 0.06rem solid #efefef;
        margin:2rem auto 3.25rem ;
    }
    .recognition .text{
        text-align: center;
        font-size: 0.24rem;
        margin-bottom: 1.33rem;
    }
    .recognition .copyright{
        width: 100%;
        text-align: center;
        font-size: 0.14rem;
        position: absolute;
        left: 0;
        bottom: 0.53rem;
    }

</style>



